import { Suspense } from "react";
import { Outlet } from "react-router-dom";
import { Header } from "../components/header/Header";
import { Footer } from "../components/footer/Footer";

const Layout = () => {
  return (
    <Suspense fallback={null}>
      <Header />
      <Outlet />
      <Footer />
    </Suspense>
  );
};

export default Layout;
